import React from "react"
import ArticleImage from "./ArticleImage"

const Article1 = () => (
  <div>
    <ArticleImage src="/images/1.gif" />
    <p className="article-preface">
      You can click the folling link to interact with a Facebook Messenger implementation of <i>Stanley the Chatbot</i>:
      <br />
      <a
        href="https://www.messenger.com/t/letthetalenttalk/"
        target="_blank" rel="noopener noreferrer"
      >
        https://www.messenger.com/t/letthetalenttalk/
      </a>
      <br />
      This implementation helps Talkpush (the company that creates <i>Stanley the Bot</i>) recruit talents. The chatbot have been updated since I left the project, but its core features still remain the same.
      <br /><br />
      You can also click the following link to visit its official page for some information:
      <br />
      <a
        href="https://talkpush.com/chatbot.html"
        target="_blank" rel="noopener noreferrer"
      >
        https://talkpush.com/chatbot.html
      </a>
    </p>
    <p className="article-subhead">
      The Problem
    </p>
    <p>
      For organizations that interview high volumes of candidates such as call centers, hotels and business process outsourcing companies, arranging first-round screening is a real pain. Scheduling and conducting interviews and managing information generated during interviews are typical processes that soak up a great deal of time and resources. Conventionally, phone interview is thought to be an comparatively desirable option, even with which a large portion of HR personnel is allocated to make hundreds of phone calls asking the same questions repeatedly while taking notes in a rush. What a drag!
      <ArticleImage src="/images/1-1.jpg" />
    </p>
    <p className="article-subhead">
      The Solution
    </p>
    <p>
      With <i>Stanley the Chatbot</i>, a job applicant can discover job openings, provide personal information, and conduct interviews via their favourite instant messaging apps including Facebook Messenger and WeChat and this service can be easily integrated with any Facebook or WeChat account of a company.
    </p>
    <p className="article-subhead">
      Software Development
    </p>
    <p>
      The solution is developed by <i>Talkpush</i>, a Manila-based startup invested by <i>500 startups</i>. When the company was still based in Hong Kong, I joined the team and worked as the major developer of <i>Stanley the Bot</i>.
    </p>
    <p>
      When I just joined the team, our solution was mainly based on automated phone calls which left much to be desired. At that time, Facebook just lauched their chatbot platform, so we decided to give it a try and I took the responsibility of developing the chatbot. This was a challenging task as there was little information about developing such a product both from design and engineering prospective. Basically, we need to figure all things out by ourselves.
    </p>
    <p>
      After about a month of intensive development, the first beta version was developed released:
      <iframe title="chatbot" width="560" height="315" className="article-video"  src="https://www.youtube.com/embed/Ct2Iks0co64" frameborder="0" allowfullscreen></iframe>
    </p>
    <p>
      The service was developed with the excellent object-oriented programming language, <a href="https://www.ruby-lang.org/" target="_blank" rel="noopener noreferrer">Ruby</a>. It was connected with an Facebook app we created at <a href="https://developers.facebook.com/" target="_blank" rel="noopener noreferrer">Facebook for Developers</a> via a webhook endpoint from our <a href="http://rubyonrails.org/" target="_blank" rel="noopener noreferrer">Rails</a> application.
    </p>
    <p>
      The basic idea is that our client (companies that recruit) would connect their Facebook account to our service and the chatbot will take over their messenger which means that unless the chatbot is disabled under certain circumstances, whenever someone talks to their Facebook account, he would be actually talking to <i>Stanley</i> (the chatbot). Facebook will relay relevant data to our Rails application, then <i>Stanley</i> will determine what does the message mean, who sent it, what's the current state of his application and etc, and then generate replies based on all these informatio and instruct Messenger to reply them.
    </p>
    <p>
      If an applicant wants to apply for a job, the chatbot will display a carousel of job openings according to the campaigns that recruiters created in the Talkpush system. After choosing a campaign, the applicant will asked to enter their personal information like phone numbers (if he has already provided it before, we will identify him and skip this step). Then he can choose either to be called immediately or schedule a call to take an interview. Later on, I upgraded the chatbot so that an applicant can record audio and video directly with Messenger to take the interview. And I also developed the WeChat version of <i>Stanley</i>.
    </p>
    <p>
      As a developer, I was also actively involved in the design process as the team maintains a relatively flat structure. With this project, I really witnessed the difficult chanllenges posed in regard to user experience and the significance of good UX design. As <i>Stanley the Bot</i> is probably the first of its kind as a recruiting tool, different people have different problems interacting with it and we cannot foresee most of them. For example, although <i>Stanley</i> would clearly indicate that he's not an actual human being, many applicants still fail to interact with it according to instructions but talk to him freely as they would when talking to a real person. While we tried hard to bring AI capability to address this problem, we were also able to mitigate it by redesigning the experience. For example, the chatbot would abdicate by itself and notify the applicant that an actual person would take care of him later on when he has responded improperly during the same state for 3 times.
    </p>
    <p>
      Besides the applicants, the major end users who interact with the chatbot, companies that recruits are also users of the chatbot. What's amazing about the chatbot is that, these companies can set up and even customize the chatbot effortlessly without any IT personnel involved. All they need to do is to log into Talkpush system, click a button to connect their Facebook account and authorize us, then check which modules they'd like to enable (e.g. if they want to enable in-app interview directly, they can just check the corresponding checkbox) and it's done. Even if the client has special needs we cannot satisfy with existing modules, we can write a custom module and implement within a day. It is designed to be flexible (functions can be appended and removed easily) as our client always have different business needs. Once set up, <i>Stanley</i> would work 24-7 talking to applicants and generating profiles. This frees recruiters from making phone calls and let them focus on processes that really require their expertise.
    </p>
  </div>
)

export default Article1



