import React from "react"
import "./ArticleImage.css"

const ArticleImage = ({ src, children }) => (
  <p className="article-image-wrapper">
    <img className="article-image" {...{ src }} alt={src} />
    <br />
    {children}
  </p>
)

export default ArticleImage



