import React from "react"
import ArticleImage from "./ArticleImage"

const Article3 = () => (
  <div>
    <ArticleImage src="/images/3-1.png" />
    <p>
      While e-commerce has revolutionized the way we shop, offline experience is still irreplacable in many circumstances. And <i>Omnitech</i> by <i>Tofugear</i>, a retail solution named as Microsoft HK ISV Solution Partner of the Year, attempts to utilize cutting-edge technologies to bring us a whole new off-line shopping experience.
    </p>
    <p>
      The <i>Omnitech</i> front end app we developed was implemented on interactive kiosks (we call them "smart mirrors") and tablets like iPads, acting as the main UI that interacts with customers. I was one of the two major developers. I supported the lead developer to refactor the app with Redux to enable more complex interactions while keeping the app easy to maintain and test. I also developed several features including the help screen which overlays on top of the UI and implemented several UI styles and animations based on designers' design.
    </p>
    <p>
      When a customer enters fitting room, our application will determine what products are brought in based on RFID and smart mirror placed in the fitting room will display detailed information about the product. So if the customer finds the pair of jeans doesn't fit, they don't step out of the room to fetch the shop assistant and wait for several minutes for him to check the inventory. They can now clearly view the inventory of other sizes on the smart mirror and simply click "Try On" button then the product will be brought to him. He can also view all the products within the store and "Try On" any of them and add it to the virtual shopping cart. Finally, they can check out with their preferred payment methods including PayPal, Apple pay, credit card and etc directly with the mirror rather than walk a long way to the cashier and wait in the queue to pay the bill.
    </p>
  </div>
)

export default Article3



