import React from "react"
import ArticleImage from "./ArticleImage"
import { Link } from "react-router-dom"

const Article6 = () => (
  <div>
    <ArticleImage src="/images/6.gif" />
    <p className="article-preface">
      <strong>
        This refer to the website you are visiting right now: <a href="https://stanleysq.github.io/" target="_blank" rel="noopener noreferrer">https://stanleysq.github.io/</a>
        <br />
        Source code: <a href="https://github.com/stanleysq/cahiers" target="_blank" rel="noopener noreferrer">https://github.com/stanleysq/cahiers</a>
      </strong>
    </p>
    <p className="article-preface">
      <strong>Responsibilities:</strong>
      <br />
      designing and developing the application with open source software solely
    </p>
    <p className="article-subhead">
      Engineering
    </p>
    <p>
      Similar to <Link to='/portfolio/5'>my playground app</Link>, this app is developed with React and client-side routing is implemented so that a user can navigate the website smoothly without built-in browser features such as bookmarking being compromised.
    </p>
  </div>
)

export default Article6



