import React from "react"
import ArticleImage from "./ArticleImage"

const Article5 = () => (
  <div>
    <ArticleImage src="/images/5.gif" />
    <p className="article-preface">
      <strong>
        The application is hosted at: <a href="http://45.248.87.15/en-us/" target="_blank" rel="noopener noreferrer">http://45.248.87.15/en/</a>
        <br />
        Source code: <a href="https://github.com/stanleysq/cahiers" target="_blank" rel="noopener noreferrer">https://github.com/stanleysq/cahiers</a>
      </strong>
    </p>
    <p className="article-preface">
      <strong>Responsibilities:</strong>
      <br />
      designing and developing the application with open source software solely
    </p>
    <p className="article-subhead">
      Front-End
    </p>
    <p>
      The project consists of 2 major parts which communicate with each other: a front-end single page application, and a back-end JSON API.
    </p>
    <p>
      The front-end part is developed with React, React Router, Redux, redux-saga, webpack, ES6, JavaScript, and so on. It is a single page application, which means that when you click on links to navigate through the app, it hardly requires the browser to refresh the page. Some elements are kept on the page such as the side bar while other parts are substituted such as the content of the list, which brings users much smoother and more pleasant experiences.
    </p>
    <p>
      Besides back-end routing, I also implemented front-end routing with React Router. Without front-end routing, no matter how users interact with the web app, the URL doesn’t change. This means that a user cannot use features like bookmarking (you can only bookmark the default page of the app) and navigate forward and backward in the browser history and when he refreshes the page, the web app is reset to the default page and he has to repeat some of his interactions to recover the previous state. But with front-end routing implemented in my app, URL changes in response to interactions. For example, you can bookmark the second division within a list as the URL changes to http://45.248.87.15/en/lists/the-index/divisions/2/ when you navigate to that division.
    </p>
    <p className="article-subhead">
      Back-End
    </p>
    <p>
      The back-end API is developed with Ruby on Rails. At the moment, I manipulate the data with YAML files, which is both friendly to human and computer. I wrote a module that knows how to interpret the content of the YAML file (in a particular format I designed), identify existing data in the database and make necessary changes.
    </p>
    <p>
      The back-end API is developed with Ruby on Rails. At the moment, I manipulate the data with YAML files, which is both friendly to human and computer. I wrote a module that knows how to interpret the content of the YAML file (in a particular format I designed), identify existing data in the database and make necessary changes.
    </p>
    <p>
      Though based on Ruby on Rails, the JSON API is not a conventional Rails app, since it is powered by a cutting-edge data query language called GraphQL and thus doesn’t entirely conform to the MVC (model–view–controller) architectural pattern. GraphQL reduces round trips for fetching data and works especially well with client-side heavy applications as it allows the client-side to specify data needs in a declarative way. This is a GraphQL query requesting for all of current user’s starred lists:
    </p>
    <p className="article-code">
      query starredLists &#123;<br />
      &nbsp;&nbsp;starredLists &#123;<br />
      &nbsp;&nbsp;&nbsp;&nbsp;divisions &#123;<br />
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;sections &#123;<br />
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;items &#123;<br />
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;... on ListItem &#123;<br />
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;id<br />
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;type<br />
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;title<br />
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;position<br />
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;starred<br />
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&#125;<br />
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&#125;<br />
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&#125;<br />
      &nbsp;&nbsp;&nbsp;&nbsp;&#125;<br />
      &nbsp;&nbsp;&#125;<br />
      &#125;
    </p>
    <p className="article-subhead">
      Production
    </p>
    <p>
      The app is hosted with Nginx and Puma on a CentOS (Linux) server provisioned by me. And I’ve also wrote some customized tasks for deployment and maintenance.
    </p>
  </div>
)

export default Article5



